import React, { Component } from 'react'
import { connect } from 'react-redux'
import { getByYear, getClosing } from '../../actions/main'
import { Available } from './available'
import { Table } from './year-details'

class Home extends Component {
  constructor(props) {
    super(props)
    let now = new Date()
    this.state = {
      isYearFound: false,
      year: now.getFullYear(),
    }
  }
  handleChange  = (event) => {
    const name = event.target.name;
    const value = event.target.value;
    this.setState ({
      [name]: value,
    })
  }
  componentDidMount() {
    this.props.getClosing()
  }

  handleSubmit = async (event) => {
    event.preventDefault()
    this.setState({
      isYearFound: false,
    })
    this.props.closing.forEach((element) => {
      if(element.year === +this.state.year) {
        this.setState ({
          isYearFound: true,
        })
      }
    })
  }

  render() {
    return (
      <div className = 'd-flex flex-column justify-content-around'>
        <Available balance = {this.props.balance}/>
        <div className = 'home-head d-flex justify-content-around'>
          <div>
            <p className = 'input-p'>Select year</p>
          </div>
          <div>
            <form className = 'form-inline' onSubmit = {this.handleSubmit}>
              <input type = 'number' maxLength = '4' className = 'form-control' value = {this.state.year} placeholder = 'Enter Year' name = 'year' onChange = {this.handleChange}/>
              <button className = 'btn btn-primary' type = 'submit'>Search</button>
            </form>
          </div>
        </div>
        <div>
          <Table found = {this.state.isYearFound} year = {this.state.year} closing = {this.props.closing.filter(entry => entry.year === +this.state.year)}/>
        </div>
      </div>
    )
  }
}

function mapStateToProps (state) {
  return {
    closing: state.closing,
    balance: state.balance
  }
}

export default connect (mapStateToProps, { getByYear, getClosing })(Home)
