import React, { Component } from 'react'
import { save } from '../../actions/main'
// import { Redirect } from 'react-router-dom'
// import { Available } from './available'
import { connect } from 'react-redux'

function PopUp(props) {
  // if (props.success) {
  //   return (
  //     <p>Entry Successfull</p>
  //   )
  // } else return null
  return <p>{ props.text }</p>
}

class Update extends Component {
  constructor(props) {
    super(props)
    let now = new Date()
    let dMonth = now.getMonth()+1 > 9 ? now.getMonth()+1 : `0${now.getMonth()+1}`
    let dDate = now.getDate() > 9 ? now.getDate() : `0${now.getDate()}`
    this.state = {
      redirect: false,
      date: `${now.getFullYear()}-${dMonth}-${dDate}`,
      description: '',
      amount: '',
      type: 'credit',
      success: false
    }
  }

  handleChange = (event) => {
    const name = event.target.name;
    const value = event.target.value;
    this.setState({
      [name]: value
    })
  }
  handleSubmit = async (event) => {
    event.preventDefault()
    try {
      await save(this.state)
      // this.setState({
      //   redirect: true,
      // })
      this.setState({
        description: '',
        amount: '',
        type: 'credit',
        success: true
      })
      setTimeout(() => this.setState({
        success: false
      }), 2000)
    } catch (err) {
      this.setState({
        failure: true,
      })
      setTimeout(() => this.setState({
        failure: false,
      }), 2000)
      console.log(err);
      throw err
    }
  }
  render() {
    return (
        <div className = 'home-head d-flex flex-column justify-content-around'>
          <form>
            <div className = 'form-group'>
              <label>Date of Transaction</label>
              <input type = 'date' className = 'form-control' name = 'date' value = {this.state.date} onChange = {this.handleChange}/>
            </div>
            <div className = 'form-group'>
              <label>Type</label>
              <select className = 'custom-select' name = 'type'  onChange = {this.handleChange} value = {this.state.type}>
              <option value = 'credit'>credit</option>
              <option value = 'debit'>debit</option>
              </select>
            <div className = 'form-group'>
              <label>Description</label>
              <input type = 'text' className = 'form-control' placeholder = 'Few details' name = 'description' value = {this.state.description} onChange = {this.handleChange}/>
            </div>
            </div>
            <div className = 'form-group'>
              <label>Amount</label>
              <input type = 'number' placeholder = '500' className = 'form-control' name = 'amount' value = {this.state.amount} onChange = {this.handleChange}/>
            </div>
            {this.state.failure ? <PopUp text = 'Something went wrong' />: null }
            { this.state.success ? <PopUp text = 'Entry Successfull'/> : null }
            <button type = 'submit' className = 'btn btn-primary' onClick = {this.handleSubmit}>Save</button>
          </form>
        </div>
      // </div>
    )
  }
}

function mapStateToProps (state) {
  return {
    closing: state.closing,
    balance: state.balance
  }
}

export default connect (mapStateToProps)(Update)
