import React, { Component } from 'react'
import { BrowserRouter, Route, Switch } from 'react-router-dom'

import Header from './other/header'
import Home from './other/home'
import Details from './other/details'
import Update from './other/update'
import Footer from './other/footer'



class App extends Component {
  render() {
    return (
      <div className = 'main'>
        <BrowserRouter>
        <div className = 'mx-auto d-flex flex-column justify-content-around'>
          <Header/>
          <Switch>
            <Route exact
              path = '/'
              render = {() => <Home/> }
            />
            <Route exact
              path = '/details/:year/:month'
              component = {Details}
            />
            <Route exact
              path = '/update'
              component = {Update}
            />
          </Switch>
          <Footer/>
          </div>
        </BrowserRouter>
      </div>
    )
  }
}

export default App
