import React from 'react'
import { Link } from 'react-router-dom'

export const Table = (props) => {
  
  if(!props.found) return null;
  else return (
    <table className = 'table'>
      <thead>
        <tr>
          <th>Month</th>
          <th>Expenses</th>
          <th>Funds</th>
          <th>Closing</th>
          <th>Actions</th>
        </tr>
      </thead>
      <tbody>
        {props.closing.map((entry, i) => {
          return (
            <tr key = {i}>
              <th>{entry.month}</th>
              <td>{entry.expenses}</td>
              <td>{entry.funds}</td>
              <td>{entry.closing}</td>
              <td className = 'd-flex justify-content-around'>
                <Link to = {`/details/${entry.year}/${entry.month}`} className = 'btn btn-primary'>Details</Link>                
              </td>
            </tr>
          )
        })}
      </tbody>
    </table>
  )
}
