import React, { Component } from 'react'
import { connect } from 'react-redux'
import { getDetailsByYearAndMonth } from '../../actions/main'
import { Available } from './available'
import { CSVLink } from 'react-csv';


class Details extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [{
        date: '2010-04'
      }],
      currentPage: 1,
      limit: 10,
    }
  }

  async componentDidMount () {
    const month = this.props.match.params.month
    const year = this.props.match.params.year
    const data = await getDetailsByYearAndMonth(year, month);
    this.setState({
      data
    })
  }

  changePage = (e) => {
    this.setState({
      currentPage: +e.target.id,
    })
  }

  render() {
    const lastIndex = this.state.currentPage * this.state.limit;
    const firstIndex = lastIndex - this.state.limit;
    const currentData = this.state.data.slice(firstIndex, lastIndex);
    const pages = [];
    for (let i = 1; i <= Math.ceil(this.state.data.length/this.state.limit); i++) {
      pages.push(i);
    }
    return (
      <div className = 'd-flex flex-column justify-content-around'>
        <Available balance = {this.props.balance}/>
        <CSVLink data = {this.state.data} filename = {`${this.state.data[0].date.substring(0, 7)}.csv`} className = 'btn btn-primary'>Download</CSVLink>
        <div className = 'home-head d-flex flex-column justify-content-around'>
          <table className = 'table'>
            <thead>
              <tr>
                <th>Date</th>
                <th>Description</th>
                <th>Amount</th>
                <th>Type</th>
              </tr>
            </thead>
            <tbody>
              {currentData.map((element, i) => {
                return (
                  <tr key = {i}>
                    <td>{element.date}</td>
                    <td>{element.description}</td>
                    <td>{element.amount}</td>
                    <td>{element.type}</td>
                  </tr>

                )
              })}

            </tbody>
          </table>
          <div className = 'd-flex justify-content-around'>
            <p>current page: {this.state.currentPage}</p>
            <ul className = 'd-flex list-unstyled justify-content-end'>
              {pages.map((i) => {
                return (
                  <li className = 'mr-3' onClick = {this.changePage} id = {i} key = {i}>{i}
                  </li>
                )
              })}
            </ul>
          </div>
        </div>
      </div>
    )
  }
}

function mapStateToProps (state) {
  return {
    closing: state.closing,
    balance: state.balance
  }
}

export default connect (mapStateToProps)(Details)
