import { url } from '../config/url-config'
import downloadjs from 'downloadjs'
// import * as fs from 'fs';

export const GETBYYEAR = 'GETBYYEAR'
export const GETCLOSING = 'GETCLOSING'
export const GETAVAILABLE = 'GETAVAILABLE'

export const getByYear = (entry) => {
  return async (dispatch) => {
    const res = await fetch(`${url}/get/${entry.year}`, {
      method: 'GET',
      mode: 'cors'
    })
    const data = await res.json()
    dispatch({
      type: GETBYYEAR,
      payload: data,
    })
  }
}

export const getClosing = () => {
  return async (dispatch) => {
    const res = await fetch(`${url}/get-closing`, {
      mehtod: 'GET',
      mode: 'cors',
    })
    const data = await res.json();
    dispatch({
      type: GETCLOSING,
      payload: data.closing,
    })
    dispatch({
      type: GETAVAILABLE,
      payload: data.available[0],
    })
  }
}

export const getDetailsByYearAndMonth = async (year, month) => {
  const data = await fetch(`${url}/get/${year}/${month}`, {
    method: 'GET',
    mode: 'cors',
  })
  return await data.json();
}

export const save  = async (data) => {
  const res = await fetch(`${url}/save`, {
    method: 'POST',
    mode: 'cors',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(data),
  }, data)
  return await res.json();
}
