import {
  GETBYYEAR,
  GETCLOSING,
  GETAVAILABLE
} from '../actions/main'

export const closing = ((state = [], action) => {
  switch (action.type) {
    case GETCLOSING: //change here
      return Object.assign([], state, action.payload)
    default: return state
  }
})

export const balance = ((state = {}, action) => {
  switch(action.type) {
    case GETAVAILABLE:
      return Object.assign({}, state, action.payload)
    default: return state
  }
})
